import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';

import { getTrendingCoinsDetails } from '../../store/actions/actionSearch';

const useStyles = makeStyles((theme) => ({
  searchContainer: {
    flex: 1,
    height: '100%',
    backgroundColor: '#eeeeee',
    overflowY: 'auto',
    paddingBottom: 15,
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    flexWrap: 'wrap',
    paddingLeft: 15,
    paddingRight: 15,
    width: '100%',
  },
  searchHeader: {
    height: '10%',
    display: 'flex',
    alignItems: 'center',
    width: '100%',
  },
  searchHeading: {
    color: '#979797',
    display: 'flex',
    fontSize: 15,
    fontWeight: 700,
  },
  trendingCoin: {
    backgroundColor: 'white',
    width: '47.5%',
    height: 200,
    marginBottom: 15,
    padding: 10,
    boxShadow: '0px 2px 9px 0px rgb(0 0 0 / 2%)',
    borderRadius: '3px',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-start',
    alignItems: 'center',
  },
  coin: {
    width: 50,
    height: 50,
    border: '2px solid #979797',
    borderRadius: '50%',
  },
  textBox: {
    margin: '5px 0px',
    textAlign: 'center',
  },
  name: {
    fontSize: 14,
    fontWeight: 600,
    marginBottom: 5,
  },
}));

export const Search: React.FC<{}> = (props) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const trendingCoins = useSelector((state: any) => state.search.trendingCoins);
  const coinsDetails = useSelector((state: any) => state.search.coinsDetails);

  useEffect(() => {
    if (trendingCoins.length > 0 && !coinsDetails) {
      dispatch(getTrendingCoinsDetails(trendingCoins));
    }
  }, []);

  console.log(trendingCoins);

  /** 
   * circulating_supply: 18185084.75
     cmc_rank: 2296
     date_added: "2017-06-23T00:00:00.000Z"
     id: 1736
     last_updated: "2021-08-28T04:12:28.000Z"
     max_supply: 19276800
     name: "Unify"
     num_market_pairs: 3
     platform: null
     quote:
      USD:
        fully_diluted_market_cap: 95712.22
        last_updated: "2021-08-28T04:12:28.000Z"
        market_cap: 90291.68890012956
        market_cap_dominance: 0
        percent_change_1h: 407.53432469
        percent_change_7d: 409.20260093
        percent_change_24h: 431.39325611
        percent_change_30d: 212.81024302
        percent_change_60d: 103.78442913
        percent_change_90d: -36.09832533
        price: 0.0049651508443
        volume_24h: 0
        slug: "unify"
symbol: "UNIFY"
tags: []
total_supply: 18185084.75

   * 
  */

  return (
    <div className={classes.searchContainer}>
      {trendingCoins.length > 0 &&
        coinsDetails &&
        Object.keys(trendingCoins).length > 0 && (
          <>
            <div className={classes.searchHeader}>
              <span className={classes.searchHeading}>Trending Now</span>
            </div>
            {trendingCoins.map((coin: any) => {
              const logo = coinsDetails[coin.id].logo;
              return (
                <div key={coin.id} className={classes.trendingCoin}>
                  <img
                    className={classes.coin}
                    src={logo}
                    alt={`${coin.slug}-logo`}
                  />
                  <div className={classes.textBox}>
                    <span className={classes.name}>{coin.name}</span>
                  </div>
                </div>
              );
            })}
          </>
        )}
    </div>
  );
};

export default Search;
